<template>
  <div>
    <div class="direction-title-bar">
      <el-tag color="#133977">{{ title }}</el-tag>
      <el-button type="primary" @click="refresh">刷新</el-button>
    </div>
    <el-table :data="grandTable" style="width: 100%">
      <el-table-column prop="name" label="车次"> </el-table-column>
      <el-table-column
        v-for="(name, index) in allStations"
        :key="index"
        :prop="name"
        :label="name"
      >
        <template slot-scope="scope">
          {{ scope.row[name] }}
          <!-- <i v-show="scope.row[name] == true" class="el-icon-success"></i>
          <i v-show="scope.row[name] == false" class="el-icon-minus"></i> -->
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
  export default {
    name: "TimeTable",
    props: ["table", "title"],
    data() {
      return {
        train: {
          name: "",
        },
        grandTable: [],
        allStations: [],
      };
    },
    mounted: function() {
      console.log("time table tbale shang", this.table);
      this.allStations = this.table.map((item) => item.stationName);
    },
    methods: {
      refresh() {
        (this.uniqueTrains = []),
          (this.allStations = []),
          (this.grandTable = []);
        this.allStations = this.table.map((item) => item.stationName);
        console.log("refresh", this.table);
        let uniqueTrains = this.getUniqueTrains(this.table);
        // console.log("unique train number:", uniqueTrains);
        // console.log("s103:", this.stationHaveThisTrain(this.table[0], "S108"));
        // console.log("s103:", this.formOneTrain("S103", this.table));

        uniqueTrains.forEach((train) => {
          let oneTrain = this.formOneTrain(train, this.table);
          this.grandTable.push(oneTrain);
        });
        console.log("the grand table:", this.grandTable);
      },
      getUniqueTrains(table) {
        let trains = [];
        table.forEach((item) => {
          item.depart.forEach((i) => {
            let train = i.type.split("次")[0];
            if (trains.indexOf(train) < 0) {
              trains.push(train);
            }
          });
        });
        return trains;
      },
      stationHaveThisTrain(station, train) {
        let departs = station.depart.map((item) => item.type.split("次")[0]);
        let index = departs.indexOf(train);
        if (index >= 0) {
          return station.depart[index].time;
        } else {
          return "";
        }
      },
      formOneTrain(name, table) {
        let res = {
          name: name,
        };
        table.forEach((station) => {
          let time = this.stationHaveThisTrain(station, name);
          if (time != "") {
            res[station.stationName] = time;
          } else {
            res[station.stationName] = "-";
          }
        });
        return res;
      },
    },
  };
</script>
